
import PhoneInput from "react-phone-number-input";
import 'react-phone-number-input/style.css'
import { useState } from 'react';
import './create_account.css'

function CreateAccount() {
    const [email, setEmail] = useState('');
    const [password1, setPass1] = useState('');
    const [password2, setPass2] = useState('');
    const [first, setFirst] = useState('');
    const [last, setLast] = useState('');
    const [phone, setPhone] = useState('');
    const [team_code, setTeam] = useState('');
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const joinTeamStatus = urlParams.get('action') === 'joinTeam';
    const createTeamStatus = urlParams.get('action') === 'createTeam';
    const httpHandler = require('react-http-client');


    function loginFail(message_str){
        document.getElementById("userMessage").innerText = message_str
    }

    function sendCreateRequest(e) {
        if(!joinTeamStatus) {
            setTeam("null")
        }
        const request = httpHandler.post(
            "/account/user/create", 
            {
                "first": first,  
                "last": last, 
                "email": email,
                "phone": phone, 
                "password1": password1,
                "password2": password2, 
                "join_action": joinTeamStatus,
                "team_code": team_code
            }
        ).then(
            (request) => {
                if(createTeamStatus)
                {
                    window.location.href = "/account/team";
                }
                else {
                    window.location.href = "/endzone/hub";
                } 

            }, 
            (request) => {
                var response = JSON.parse(request.message)
                var message_str = ""
                if (response.status >= 404) 
                {
                    message_str = "User Creation Failed: contact endzone.support@gmail.com"
                }
                else 
                {
                    message_str = response.data
                }
                loginFail(message_str);
                console.log(message_str);
            }
        );
    }

    function handleSubmit(e) {
        document.getElementById("userMessage").innerText = ""
        sendCreateRequest(e)
    }

    return (
        <div>
            <div className = "create-account-container">
                <form className="register-form" onSubmit={handleSubmit}>
                    <label className="input-title" htmlFor="first-name">First Name</label>
                    <input className="input-box" value={first} onChange={(e) => setFirst(e.target.value)}/>
                    
                    <label className="input-title" htmlFor="last-name">Last Name</label>
                    <input className="input-box" value={last} onChange={(e) => setLast(e.target.value)}/>

                    <label className="input-title" htmlFor="email">Email</label>
                    <input className="input-box" value={email} onChange={(e) => setEmail(e.target.value)}/>
                    <label className="input-title" htmlFor="phone">Phone Number</label>
                    <PhoneInput defaultCountry="US" value={phone} onChange={setPhone}/>
                    
                    <label className="input-title" htmlFor="password">Password</label>
                    <input className="input-box" type="password" value={password1} onChange={(e) => setPass1(e.target.value)}/>
                    
                    <label className="input-title" htmlFor="confirm-pass">Confirm Password</label>
                    <input className="input-box" type="password" value={password2} onChange={(e) => setPass2(e.target.value)}/>

                    {joinTeamStatus ? (
                        [
                            <label className="input-title" htmlFor="team">Team Code</label>,
                            <input className="input-box" value={team_code} onChange={(e) => setTeam(e.target.value)}/>
                        ]
                    ) : (
                       <></>
                    )}
                    <div className = "button-container">
                        <button className="submit-button" type="button" onClick={handleSubmit}>Create Account</button>
                    </div>
                    <p id = "userMessage"></p>
                </form>
            </div> 
        </div>
    );
}

export default CreateAccount;
